.notFound {
  position: relative;
  width: 100%;
  /* min-height: 100vh; */

  display: block;
  justify-content: center;
  align-items: center;
  align-self: center;
}
.notFound__content {
  display: grid;
}
.notfound__title {
  font-size: 160px;
  font-weight: bold;
  color: #535761;
}

.notfound__desc {
  font-size: 30px;
  font-weight: bold;
  color: #8a8f99;
}
.notFound__content a {
  display: flex;
  justify-content: center;
  font-size: 20px;
  color: #346cab;
  font-weight: 500;
  text-decoration: none;
}

@media (max-width: 768px) {
  .notFound {
    height: 90vh;
  }
}
